import React from 'react';
import { baseLinkUrl } from '../../PlaygroundState';
import { Card } from '../Card';
import { LocalIcon } from '../LocalIcon';
import { OnePlayerIcon } from '../OnePlayerIcon';

import './css-anims.scss';

export const CSSAnimsCard: React.FC = () => {
  return (
    <Card
      logo={<div className={'css-anims logo'}></div>}
      title={'CSS Anims'}
      icons={[<OnePlayerIcon />, <LocalIcon />]}
      blurb={'Collection of css spinners and animated drawings I do for fun.'}
      link={baseLinkUrl + 'css-anims'}
    />
  );
};
